.parallaxLogo {
    /* The image used */
    background-image: url("../../landingPageLogo.png");
    /* Set a specific height */
    width: 100%;
    height: 500px; 
  
    /* Create the parallax scrolling effect */
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: 500px;
}
.logoWrapper{
    text-align: center;
    
}
.landingPageLogo{
    max-width: 60%;
    object-fit: contain;
}

.landingPage{
    margin-top: 600px;
}